.invited {
  border-top: 4rpx solid #ccc;
}
.choosepeople {
  padding-right: 20rpx;
}
.choosepeople::after {
  right: 0;
  height: 16rpx;
  width: 16rpx;
}
.mask-bottom-wrap {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0 24rpx;
  view {
    line-height: 114rpx;
    width: 100%;
    box-sizing: border-box;
    background-color: #fff;
    text-align: center;
    border-radius: 20rpx;
    font-size: 40rpx;
    color: #000;
  }
  view:nth-child(1) {
    border-bottom-left-radius: 0rpx;
    border-bottom-right-radius: 0rpx;
    border-bottom: 1rpx solid #d7d7d7;
  }
  .qwe {
    margin-bottom: 16rpx;
    border-top-left-radius: 0rpx;
    border-top-right-radius: 0rpx;
  }
  view:nth-last-child(1) {
    color: #157efb;
    margin-bottom: 20rpx;
  }
}
